.grid-container {
  display: grid;
  grid-template-columns: [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px [col] 100px;
  grid-template-rows: [row] auto [row]auto [row];
  background-color: #2196f3;
  padding: 10px;
}
.grid-item {
  background-color: burlywood;
  border: 1px solid gold;
  padding: 20px;
  font-size: 30px;
  text-align: center;
}

.grid-item-current {
  border: 1px solid darkgoldenrod;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  /* color: cornsilk; */
}
